.mobile-menu {
  position: fixed;
  z-index: 3;
  top: 0;
  right: 0;
  width: 250px;
  height: 100%;
  overflow: auto;

  background-color: #e17992;

  transform: translateX(220%);
  opacity: 0;
  transition: transform 750ms linear, opacity 750ms linear;

  @media screen and (min-width: 768px) {
    width: 260px;
  }

  &.is-open {
    filter: drop-shadow(-8px 0px 20px rgba(0, 0, 0, 0.25));
    transform: translateX(0);
    opacity: 1;
    transition: transform 750ms linear, opacity 750ms linear;
  }
}

.btn-light {
  display: flex;
  align-items: center;
  text-align: center;

  background-color: $background-secondary-color;
  border: 0;
  cursor: pointer;
  color: $primary-accent-color;

  transition: background-color 250ms linear;

  &:hover,
  &:focus {
    background-color: $background-secondary-color;
  }
}

.mobile-menu__button {
  justify-content: center;
  margin-top: 80px;
  margin-bottom: 32px;
  width: 210px;
  margin-right: auto;
  margin-left: auto;
  padding: 13px 0;

  border-radius: 22px;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.313;

  @media screen and (min-width: 768px) {
    width: 186px;
    margin-top: 120px;
  }
}
.mobile-menu__icon {
  margin-left: 22px;
}

.mobile-menu__item {
  display: flex;
  align-items: center;
  padding-left: 21px;
  height: 42px;

  border-top: 1px solid #e18298;
}

.mobile-menu__item:last-child {
  border-bottom: 1px solid #e18298;
}

.mobile-menu__link {
  font-weight: 700;
  font-size: 14px;
  line-height: 1.286;
  color: $primary-text-color;

  transition: color 250ms linear;

  &--current {
    color: $primary-accent-color;
  }

  &:hover,
  &:focus {
    color: $primary-accent-color;
  }
}
